import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Message, MenuItem, ConfirmationService } from "primeng/primeng";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { DecimalPipe } from '@angular/common';
import { BigCustDonateApplyBean } from '../bigCustomDonateApply/bean/bigCustDonateApply.bean';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkZzNum, decimalNumTwo, Equhundered, Equhundered2 } from 'app/@uisftech/common/service/validator';
import { TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';
/**
 * 大客户费用利息/捐赠修改
 */
@Component({
    selector: 'update-bigCustomDonateApply',
    templateUrl: './update-bigCustomDonateApply.component.html',
    styleUrls: ['./update-bigCustomDonateApply.component.scss'],
    providers: [BigCusHttpService, Commfunc, DecimalPipe, CusViewHttpService, ConfirmationService]
})
export class UpdateBigCustomDonateApplyComponent implements OnInit {
    @Input() updateValue: any;
    @Input() InValue2: any;//补录
    @Output() outValue: EventEmitter<any> = new EventEmitter();//修改
    @Output() updateCall: EventEmitter<any> = new EventEmitter();//补录
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    ch: any = CALENDAR_CN;
    fileList: any = [];//上传文件
    msgs: Message[] = [];
    UPLOAD: string = TZB_CUS_UPLOAD;//导入
    bigCustDonateApplyBean: BigCustDonateApplyBean = new BigCustDonateApplyBean();
    openacctdate: Date = null;
    //校验
    userform: FormGroup;
    code: any;
    scheduleTypeOptions: any = [];//支付方式
    depcash: string;//存款金额
    seqno: string;
    decimalNumTwo = decimalNumTwo;
    Equhundered = Equhundered;
    showBtn: boolean = true;
    showDisabled: boolean = false;//可编辑
    paramObj: any = {};
    dealPerShow: boolean = false;//选人
    waitSelUserList: any;
    outVal: any;
    Deptimelimit: any = [];
    num: number = 0;
    accountno:string;
    constructor(
        public router: Router,
        public bigCusHttpService: BigCusHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public decimalPipe: DecimalPipe,
        public httpService: CusViewHttpService,
        public confirmationService: ConfirmationService
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.scheduleTypeOptions = this.code['PAYTYPEFLAG'];//支付方式
        this.Deptimelimit = this.code['Deptimelimit'];//存款期限
    }

    ngOnInit() {
        this.userform = this.fb.group({
            // "seqno": new FormControl('', Validators.required),
            "custcode": new FormControl('', [Validators.pattern(/^[1-9]\d{0,9}$/), Validators.required]),//校验客户号输入
            "custname": new FormControl(''),
            "accountno": new FormControl('', [Validators.pattern(/^[1-9]\d*$/)]),
            "openacctdate": new FormControl(''),
            "deptimelimit": new FormControl('', Validators.required),
            "depcash": new FormControl(''),
            // "paytype": new FormControl('', Validators.required),
            "feeprice": new FormControl('', Validators.required),
            "getrate": new FormControl('', Validators.required),
            "addordonaterate": new FormControl('', Validators.required),
        });
        this.eacho();
    }

    ngOnChanges() {
        if (this.updateValue) {
            this.seqno = this.updateValue.seqno;//大客户编号(修改)
            this.showBtn = true;
        } else if (this.InValue2) {
            this.seqno = this.InValue2.seqno;//大客户编号（补录）
            this.accountno=this.InValue2.accountno;//存款账号
            this.showBtn = false;
            this.showDisabled = true;
        }
    }

    //初始化调详情
    eacho() {
        let param = {
            seqno: this.seqno
        }
        this.bigCusHttpService.bigCustInstDonateBaseQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.bigCustbaseInfo.forEach(element => {
                    if (element.seqno) {
                        this.bigCustDonateApplyBean.seqno = element.seqno//大客户编号
                    }
                    if (element.custcode) {
                        this.bigCustDonateApplyBean.custcode = element.custcode//客户号
                    }
                    if (element.custname) {
                        this.bigCustDonateApplyBean.custname = element.custname//客户名称
                    }
                    if (this.showBtn&&element.accountno) {//修改
                        this.bigCustDonateApplyBean.accountno = element.accountno//存款账号
                    }
                    if (!this.showBtn&&element.accountno) {//补录
                        if (!this.accountno) {
                            this.bigCustDonateApplyBean.accountno=null;
                        }else if (this.accountno){
                            this.bigCustDonateApplyBean.accountno = element.accountno//存款账号
                        } 
                    }
                    if (element.depcash) {
                        this.bigCustDonateApplyBean.depcash = this.decimalPipe.transform(element.depcash, '1.2-2');//存款金额
                    }
                    if (element.deptimelimit) {
                        this.bigCustDonateApplyBean.deptimelimit = element.deptimelimit//存款期限
                    }
                    if (element.getrate) {
                        this.bigCustDonateApplyBean.getrate = this.decimalPipe.transform(element.getrate, '1.4-4');//中标利率（%）
                    }
                    if (element.addordonaterate) {
                        this.bigCustDonateApplyBean.addordonaterate =  this.decimalPipe.transform(element.addordonaterate, '1.4-4');//加价利率/捐赠利率（%）
                    }
                    if (element.feeprice) {
                        this.bigCustDonateApplyBean.feeprice = this.decimalPipe.transform(element.feeprice, '1.2-2');//费用单价
                    }
                    if (element.applyuser) {
                        this.bigCustDonateApplyBean.applyuser = element.applyuser//申请用户
                    }
                    if (element.fileList) {
                        this.fileList = element.fileList;
                        this.bigCustDonateApplyBean.fileList = this.fileList;
                    }
                    if (this.showBtn&&element.openacctdate) {//修改
                        this.bigCustDonateApplyBean.openacctdate = element.openacctdate;
                    }
                    if (!this.showBtn&&element.openacctdate) {//补录
                        if (!this.accountno) {
                            this.bigCustDonateApplyBean.openacctdate=null;
                        }else if (this.accountno){
                            this.bigCustDonateApplyBean.openacctdate = element.openacctdate;//开户日期
                        } 
                    }
                });

                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //提交按钮
    subClick() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        for (let element of this.fileList ) {
            if (element.fileName==""&&element.fileUrl=="") {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '请上传附件'});
                return;
            }
        }
        this.confirmationService.confirm({
            message: '确定要提交吗?',
            accept: () => {
                this.num = 1;
                this.bigCustDonateApplyBean.flag = '1';
                this.bigCustDonateApplyBean.paytype = '00';
                this.bigCustDonateApplyBean.seqno = this.seqno;
                // this.bigCustDonateApplyBean.openacctdate = this.commfunc.transDateN(this.openacctdate);//营销时间-起
                if (this.bigCustDonateApplyBean.depcash) {
                    this.bigCustDonateApplyBean.depcash = this.commfunc.transform(this.bigCustDonateApplyBean.depcash) + '';
                }
                if (this.bigCustDonateApplyBean.feeprice) {
                    this.bigCustDonateApplyBean.feeprice = this.commfunc.transform(this.bigCustDonateApplyBean.feeprice) + '';
                }
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers;
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.waitSelUserList = this.commfunc.query('DKHLLSQ', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let chooseThis = this.waitSelUserList.display;
                    console.log(this.dealPerShow);
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a2 = commonHeader.extParam.specifyNextOpers;
                    console.log(a2);
                    if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                        this.dealPerShow = true;
                    } else {
                        this.subApprove();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }
            }
        });
    }

    //提交
    subApprove() {
        this.bigCusHttpService.updateBigCustInstDonate(this.bigCustDonateApplyBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //保存
    saveClick() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        for (let element of this.fileList ) {
            if (element.fileName==""&&element.fileUrl=="") {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '请上传附件'});
                return;
            }
        }
       
       
        this.bigCustDonateApplyBean.flag = '0';
        this.bigCustDonateApplyBean.paytype = '00';
        this.bigCustDonateApplyBean.seqno = this.seqno;
        // this.bigCustDonateApplyBean.openacctdate = this.commfunc.transDateN(this.openacctdate);//营销时间-起
        if (this.bigCustDonateApplyBean.depcash) {
            this.bigCustDonateApplyBean.depcash = this.commfunc.transform(this.bigCustDonateApplyBean.depcash) + '';
        }
        if (this.bigCustDonateApplyBean.feeprice) {
            this.bigCustDonateApplyBean.feeprice = this.commfunc.transform(this.bigCustDonateApplyBean.feeprice) + '';
        }
        this.bigCusHttpService.updateBigCustInstDonate(this.bigCustDonateApplyBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //确定补录
    reClick() {
        // this.bigCustDonateApplyBean.openacctdate = this.commfunc.transDateN(this.openacctdate);
        if (!this.bigCustDonateApplyBean.accountno) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '存款账号不能为空' });
            return;
        }
        // if (!this.bigCustDonateApplyBean.openacctdate) {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '开户日期不能为空' });
        //     return;
        // }

        this.confirmationService.confirm({
            message: '确定要补录吗?',
            accept: () => {
                this.num = 2;
                this.paramObj = {
                    seqno: this.seqno,
                    custcode: this.bigCustDonateApplyBean.custcode,
                    accountno: this.bigCustDonateApplyBean.accountno,
                    openacctdate: this.bigCustDonateApplyBean.openacctdate
                }
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers;
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.waitSelUserList = this.commfunc.query('DKHLLBL', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let chooseThis = this.waitSelUserList.display;
                    console.log(this.dealPerShow);
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a2 = commonHeader.extParam.specifyNextOpers;
                    console.log(a2);
                    if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                        this.dealPerShow = true;
                    } else {
                        this.approveClick();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }
            }
        });

    }
    //审批
    approveClick() {
        this.bigCusHttpService.bigCustInstDonateSupple(this.paramObj).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.updateCall.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        })
    }
    outDisplayCall(event) {
        console.log(event);
        this.dealPerShow = event;
    }
    outValueCall(event) {
        console.log(event);
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        console.log(commonHeader);
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        if (this.num == 1) {
            this.subApprove();//提交
        } else if (this.num == 2) {
            this.approveClick()//补录
        }
    }
    //回显客户名称
    showName(item) {
        if (item && item != '') {
            let param = {
                custNo: item
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.bigCustDonateApplyBean.custname = data.custName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //输入正确的存款账号时，开户日期自动回显
    showDate(item) {
        if (item && item != '') {
            let param = {
                accountno: item
            }
            this.bigCusHttpService.getAccountInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.bigCustDonateApplyBean.openacctdate = data.openacctdate;
                } else {
                    this.bigCustDonateApplyBean.accountno='';
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });

        }else{
            this.bigCustDonateApplyBean.openacctdate=''
        }
    }
    //上传文件
    myUploader(event) {
        if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
            this.bigCustDonateApplyBean.fileList = JSON.parse(event.xhr.response);
            this.fileList = this.bigCustDonateApplyBean.fileList;
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    onkey(event) {
        if (decimalNumTwo(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的金额!' }];
            event.target.value = '';
            return
        }

    }
    //校验百分比
    percenterBtn(event, num) {
        if (Equhundered2(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的百分比!' }];
            event.target.value = '';
            return
        }
        if (num == '1') {
            this.bigCustDonateApplyBean.getrate = this.decimalPipe.transform(this.bigCustDonateApplyBean.getrate, '1.4-4');
        } else if (num == '2') {
            this.bigCustDonateApplyBean.addordonaterate = this.decimalPipe.transform(this.bigCustDonateApplyBean.addordonaterate, '1.4-4');
        }
    }

    textSum(num) {
        if (num == '1') {
            this.bigCustDonateApplyBean.depcash = this.decimalPipe.transform(this.bigCustDonateApplyBean.depcash, '1.2-2');//存款金额
        } else if (num == '2') {
            this.bigCustDonateApplyBean.feeprice = this.decimalPipe.transform(this.bigCustDonateApplyBean.feeprice, '1.2-2');//费用单价
        }
    }

}
