import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';


import { BigCustomDonateApplyComponent } from './bigCustomDonateApply/bigCustomDonateApply.component';
import { BigCustomDonateQueryComponent } from './bigCustomDonateQuery/bigCustomDonateQuery.component';
import { BigCustomDonateHistoryQueryComponent } from './bigCustomDonateHistoryQuery/bigCustomDonateHistoryQuery.component';
import { BigCustomDonateDetailComponent } from './bigCustomDonateDetail/bigCustomDonateDetail.component';//大客户捐赠详情
import { UpdateBigCustomDonateApplyComponent } from './update-bigCustDonateApply/update-bigCustomDonateApply.component';//大客户利息的修改
export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'bigCustomDonateApply',
                component: BigCustomDonateApplyComponent,
            },
            {
                path: 'bigCustomDonateQuery',
                component: BigCustomDonateQueryComponent
            },
            {
                path: 'bigCustomDonateHistoryQuery',
                component: BigCustomDonateHistoryQueryComponent,
            },
            {
                path: 'bigCustomDonateDetail',
                component: BigCustomDonateDetailComponent
            },
          
        ]
    }
]
export const BigCusDonateRouting: ModuleWithProviders = RouterModule.forChild(routes);