import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { DonateQueryBean } from './bean/bigCustDonate.bean';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';

/**
 * 大客户费用利息/捐赠查询-洪满义
*/
@Component({
    selector: 'bigCustomDonateQuery',
    templateUrl: './bigCustomDonateQuery.component.html',
    styleUrls: ['./bigCustomDonateQuery.component.scss'],
    providers: [BigCusHttpService, Commfunc, ConfirmationService, CusViewHttpService, CusOperationHttpService]
})
export class BigCustomDonateQueryComponent implements OnInit {
    checkNumber = checkNumber;//校验数字
    custNoTen = custNoTen;
    inValue: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    detailHeader: string = '大客户费用详情';
    detailPage: boolean = false;
    msgs: Message[] = []; //提示信息
    dataList: any = [];
    totalNum: number;
    donateQueryBean: DonateQueryBean = new DonateQueryBean();
    first: number = 0;//分页控制
    code: any;
    statusType: any = [];
    checkAll: boolean = false;
    checkBox: any = [];
    chooseData: any = [];
    operaTeype: any = [];
    custNo: string;
    userId: string;
    custManagerNosFlag: boolean = false;
    manageId: string;//主联系人
    addDisplay: boolean = false;
    InValue2: any = [];//补录
    dealPerShow: boolean = false;//选人
    waitSelUserList: any;
    outVal: any;
    paramObj:any={};
    bigDonate:any=[];
    constructor(
        public router: Router,
        public bigCusHttpService: BigCusHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusViewHttpService,
        public cusOperationHttpService: CusOperationHttpService
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.statusType = this.code['QUERYSTATUS'];//状态
        this.operaTeype = this.code['OPERATYPE'];//操作类型
        this.bigDonate=this.code['BIGDONATE'];//大客户捐赠
    }

    ngOnInit() {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.donateQueryBean.pageNum = 1;
        this.donateQueryBean.pageSize = 10;
        this.first = 0;
        this.queryClick();
    }

    queryClick() {
        this.donateQueryBean.pageNum = 1;
        this.donateQueryBean.pageSize = 10;
        this.first = 0;
        this.query();

    }
    //查询
    query() {
        // this.doClearCheck();
        this.bigCusHttpService.bigCustInstDonateQuery(this.donateQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.totalNum = data.totalCount;
                this.dataList = data.bigCustResult;
                this.dataList.forEach((item,index) => {
                    this.bigCusHttpService.findUser({ custNo: this.dataList[index].custcode }).subscribe(dataUser => {
                        if (dataUser.returnCode.code == 'success') {
                            if (dataUser.infoList != null) {
                                if (dataUser.infoList.length > 0) {
                                    this.manageId = dataUser.infoList[0].manageId;
                                    this.dataList[index]['manageId'] = this.manageId;
                                    // console.log( this.dataTable);
                                }
                            }
                        } 
                    })
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //重置
    resetClick() {
        this.donateQueryBean = new DonateQueryBean();
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.donateQueryBean.pageNum = 1;
        this.donateQueryBean.pageSize = 10;
        this.first = 0;
    }
    paginate　(event) {
        //每页显示的条数
        this.donateQueryBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.donateQueryBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.donateQueryBean.pageSize;
        this.query();
    }

    //删除
    toDelete(item) {
        this.confirmationService.confirm({
            message: '确认要撤销吗?',
            accept: () => {
               this.paramObj = {
                    custcode: item.custcode,
                    seqno: item.seqno,
                }
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers;
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.waitSelUserList = this.commfunc.query('DKHLLCX', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let chooseThis = this.waitSelUserList.display;
                    console.log(this.dealPerShow);
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a2 = commonHeader.extParam.specifyNextOpers;
                    console.log(a2);
                    if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                        this.dealPerShow = true;
                    } else {
                        this.approveClick();
                    }
                }else{
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }
            }
        });

    }
    //删除
    approveClick() {
        this.bigCusHttpService.bigCustInstDonateRevoke(this.paramObj).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                this.query();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        })
    }

    outDisplayCall(event) {
        console.log(event);
        this.dealPerShow = event;
    }
    outValueCall(event) {
        console.log(event);
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        console.log(commonHeader);
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.approveClick();//创建
    }

    //补录
    toDeal(item) {
        this.detailHeader = '大客户补录界面'
        this.addDisplay = true;
        this.InValue2 = item;

    }

    updateCall(param) {
        this.addDisplay = param;
        this.query();
    }

    //详情
    toDetail(data) {
        this.detailHeader = '大客户详情界面'
        this.detailPage = true;
        this.inValue = data;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }


    //校验客户号只能输入数字
    onKeyupNumber(item, event) {
        if (checkNumber(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
            event.target.value = '';
            return
        } else {
            if (item && item != '') {
                let param = {
                    custNo: item
                }
                this.httpService.selectCustBriefInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.donateQueryBean.custname = data.custName;
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        }
    }
    //校验存款账号     
    onKeyupAccountno(event) {
        if (checkNumber(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的存款账号!' }];
            event.target.value = '';
            return
        }
    }
    //校验大客户编号
    onKeyupSeqno(event) {
        if (custNoTen(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的编号!' }];
            event.target.value = '';
            return
        }
    }
        //绑定权限id
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
