import { Component, OnInit, Input } from '@angular/core';
/**
 * 捐赠详情-洪满义
 */
import { Message, ConfirmationService } from 'primeng/primeng';
import { BigCustDonateApplyBean } from '../bigCustomDonateApply/bean/bigCustDonateApply.bean';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { DecimalPipe } from '@angular/common';
@Component({
    selector: 'bigCustomDonateDetail',
    templateUrl: './bigCustomDonateDetail.component.html',
    styleUrls: ['./bigCustomDonateDetail.component.scss'],
    providers: [BigCusHttpService,Commfunc,DecimalPipe]
})
export class BigCustomDonateDetailComponent implements OnInit {
    @Input() inValue: any;
    show: number = 1;
    returnList: any = {};
    seqno: string;
    msgs: Message[] = []; //提示信息
    code: any;
    PayType: any = [];
    resultList:any=[];
    doDeal: any=[];
    fileName:string;
    Deptimelimit: any = [];
    accountno:string;//存款账号
    constructor(
        public bigCusHttpService: BigCusHttpService,
        public commfunc:Commfunc,
        public decimalPipe: DecimalPipe,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.PayType = this.code['PAYTYPEFLAG'];//支付方式
        this.doDeal = this.code['DoDeal'];
        this.Deptimelimit=this.code['Deptimelimit'];//存款期限
    }

    ngOnInit() {
        this.seqno = this.inValue.seqno;
        this.accountno=this.inValue.accountno;
        this.eachoBtn();
        // this.initClick();
        
      
    }
    // apply(num) {
    //     this.show = num;
    // }
    //回显中间的详情
    eachoBtn() {
        let param = {
            seqno: this.seqno,
        }
        this.bigCusHttpService.bigCustInstDonateBaseQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.bigCustbaseInfo.forEach(element => {
                    this.returnList = element; 
                    if (!this.accountno) {//表格没值，详情的弹出框也不应该出现存款账号
                        this.returnList.accountno=null;
                        this.returnList.accountname=null;
                        this.returnList.openacctdate=null;
                    }  
                    this.returnList.depcash = this.decimalPipe.transform(this.returnList.depcash, '1.2-2');//存款金额(万元)
                    this.returnList.feeprice = this.decimalPipe.transform(this.returnList.feeprice, '1.2-2'); //费用单价
                    this.returnList.getrate = this.decimalPipe.transform(this.returnList.getrate, '1.4-4');//中标利率(%)
                    this.returnList.addordonaterate = this.decimalPipe.transform(this.returnList.addordonaterate, '1.4-4');//加价利率/捐赠利率(%)

                });
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    //回显下面的审批数据
    // initClick () {
    //     let param ={
    //         seqno: this.seqno,
    //     }
    //     this.bigCusHttpService.bigCustInstDonateWorkQuery(param).subscribe(data => {
    //         if (data.returnCode.code == 'success') {
    //             this.resultList= data.bigCustWorkList;
    //             if (this.resultList){
    //                 this.resultList.forEach(element => {
    //                   if(element.statusId==null) {
    //                     element.statusId='';
    //                   }
    //                 });
    //               }
    //             this.msgs = [];
    //             this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    //         } else {
    //             this.msgs = [];
    //             this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    //         }

    //     }, error => {
    //         this.msgs = [];
    //         this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    //     })
    // }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }


}
