import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Message, MenuItem } from "primeng/primeng";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { DecimalPipe } from '@angular/common';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { BigCustDonateApplyBean } from './bean/bigCustDonateApply.bean';
import { ConfirmationService } from 'primeng/components/common/api';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkZzNum, checkNumber, decimalNumTwo, Equhundered, Equhundered2 } from 'app/@uisftech/common/service/validator';
/**
 * 大客户费用利息/捐赠申请-洪满义
 */
@Component({
    selector: 'bigCustomDonateApply',
    templateUrl: './bigCustomDonateApply.component.html',
    styleUrls: ['./bigCustomDonateApply.component.scss'],
    providers: [BigCusHttpService, Commfunc, DecimalPipe, CusViewHttpService, ConfirmationService]
})
export class BigCustomDonateApplyComponent implements OnInit {
    checkNumber = checkNumber;//校验数字
    checkZzNum = checkZzNum;
    decimalNumTwo = decimalNumTwo;
    Equhundered = Equhundered;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    ch: any = CALENDAR_CN;
    fileList: any = [];//上传文件
    msgs: Message[] = [];
    UPLOAD: string = TZB_CUS_UPLOAD;//导入
    bigCustDonateApplyBean: BigCustDonateApplyBean = new BigCustDonateApplyBean();
    openacctdate: Date = null;
    //校验
    userform: FormGroup;
    code: any;
    PayType: any = [];//支付方式
    depcash: string;//存款金额
    custNo: string;
    isShowTip: boolean = true;

    dealPerShow: boolean = false;//选人
    waitSelUserList: any;
    outVal: any;
    Deptimelimit: any = [];
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
    user: any;
    constructor(
        public router: Router,
        public bigCusHttpService: BigCusHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public decimalPipe: DecimalPipe,
        public httpService: CusViewHttpService,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.PayType = this.code['PAYTYPEFLAG'];//支付方式
        this.Deptimelimit = this.code['Deptimelimit'];//存款期限
    }

    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.userform = this.fb.group({
            "custcode": new FormControl('', [Validators.pattern(/^[1-9]\d{0,9}$/), Validators.required]),//校验客户号输入
            "custname": new FormControl(''),
            "accountno": new FormControl('', [Validators.pattern(/^[0-9]*$/)]),
            "openacctdate": new FormControl(''),
            "deptimelimit": new FormControl('', Validators.required),
            "depcash": new FormControl(''),
            // "paytype": new FormControl('', Validators.required),
            "feeprice": new FormControl('',Validators.required),
            "getrate": new FormControl('', Validators.required),
            "addordonaterate": new FormControl('', Validators.required),
        });
    }

    //回显客户名称
    showName(item) {
        if (item && item != '') {
            let param = {
                custNo: item
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.dataSourceId == 'BR00B' && this.user.targetId == '101011') {
                        this.bigCustDonateApplyBean.custname = data.custName;
                    } else {
                        if (data.dataSourceId == 'BR00A' && this.user.targetId == '101010') {
                            this.bigCustDonateApplyBean.custname = data.custName;
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前非台行客户!' }];
                            return
                        }
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //输入正确的存款账号时，开户日期自动回显
    showDate(item) {
        if (item && item != '') {
            let param = {
                accountno: item
            }
            this.bigCusHttpService.getAccountInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.bigCustDonateApplyBean.openacctdate = data.openacctdate;
                } else {
                    this.bigCustDonateApplyBean.accountno = '';
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        } else {
            this.bigCustDonateApplyBean.openacctdate = ''
        }
    }
    //保存
    saveClick() {
        this.isShowTip = true;
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (this.fileList.length == 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请上传附件' });
            return;
        }
        this.bigCustDonateApplyBean.flag = '0';
        this.bigCustDonateApplyBean.paytype = '00';
        // this.bigCustDonateApplyBean.openacctdate = this.commfunc.transDateN(this.openacctdate);//营销时间-起
        if (this.bigCustDonateApplyBean.depcash) {
            this.bigCustDonateApplyBean.depcash = this.commfunc.transform(this.bigCustDonateApplyBean.depcash) + '';
        }
        if (this.bigCustDonateApplyBean.feeprice) {
            this.bigCustDonateApplyBean.feeprice = this.commfunc.transform(this.bigCustDonateApplyBean.feeprice) + '';
        }

        this.bigCusHttpService.addBigCustInstDonate(this.bigCustDonateApplyBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.isShowTip = false;
                this.bigCustDonateApplyBean = new BigCustDonateApplyBean();
                this.fileList = [];
                // this.openacctdate = null;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    //发布按钮
    subClick() {
        this.isShowTip = true;
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (this.fileList.length == 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请上传附件' });
            return;
        }
        this.confirmationService.confirm({
            message: '确定要提交吗?',
            accept: () => {
                this.bigCustDonateApplyBean.flag = '1';
                this.bigCustDonateApplyBean.paytype = '00';
                if (this.bigCustDonateApplyBean.depcash) {
                    this.bigCustDonateApplyBean.depcash = this.commfunc.transform(this.bigCustDonateApplyBean.depcash) + '';
                }
                if (this.bigCustDonateApplyBean.feeprice) {
                    this.bigCustDonateApplyBean.feeprice = this.commfunc.transform(this.bigCustDonateApplyBean.feeprice) + '';
                }
                // this.bigCustDonateApplyBean.openacctdate = this.commfunc.transDateN(this.openacctdate);//营销时间-起
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers;
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.waitSelUserList = this.commfunc.query('DKHLLSQ', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let chooseThis = this.waitSelUserList.display;
                    console.log(this.dealPerShow);
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a2 = commonHeader.extParam.specifyNextOpers;
                    console.log(a2);
                    if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                        this.dealPerShow = true;
                    } else {
                        this.approveClick();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }

            }
        });
    }
    //审批
    approveClick() {
        this.bigCusHttpService.addBigCustInstDonate(this.bigCustDonateApplyBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.isShowTip = false;
                this.bigCustDonateApplyBean = new BigCustDonateApplyBean();
                this.fileList = [];
                // this.openacctdate = null;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    outDisplayCall(event) {
        console.log(event);
        this.dealPerShow = event;
    }
    outValueCall(event) {
        console.log(event);
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        console.log(commonHeader);
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.approveClick();//创建
    }
    //上传文件
    myUploader(event) {
        if (event.xhr.response) {
            // this.fileList = JSON.parse(event.xhr.response);
            if (JSON.parse(event.xhr.response).success == false) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
                return
            } else {
                this.bigCustDonateApplyBean.fileList = JSON.parse(event.xhr.response);
                this.fileList = this.bigCustDonateApplyBean.fileList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
            }
        }

    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    onkey(event) {
        if (decimalNumTwo(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的金额!' }];
            event.target.value = '';
            return
        }
    }
    textSum(num) {
        if (num == '1') {
            this.bigCustDonateApplyBean.depcash = this.decimalPipe.transform(this.bigCustDonateApplyBean.depcash, '1.2-2');//存款金额
        } else if (num == '2') {
            this.bigCustDonateApplyBean.feeprice = this.decimalPipe.transform(this.bigCustDonateApplyBean.feeprice, '1.2-2');//费用单价
        }

    }


    //校验百分比
    percenterBtn(event, num) {
        if (Equhundered2(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的百分比!' }];
            event.target.value = '';
            return
        }
        if (num == '1') {
            this.bigCustDonateApplyBean.getrate = this.decimalPipe.transform(this.bigCustDonateApplyBean.getrate, '1.4-4');//中标利率
        } else if (num == '2') {
            this.bigCustDonateApplyBean.addordonaterate = this.decimalPipe.transform(this.bigCustDonateApplyBean.addordonaterate, '1.4-4');//加价利率
        }
    }

    //重置
    resetClick() {
        this.isShowTip = false;
        this.bigCustDonateApplyBean = new BigCustDonateApplyBean();
        this.fileList = [];
        // this.openacctdate = null;
    }
    down(temp) {
        let name = temp.fileName;
        let path = temp.fileUrl;
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
    }
    //绑定权限id
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
