import { Component, OnInit } from '@angular/core';
import { HistoryQueryBean } from './bean/bigCustDonateHistoryQuery.bean';
import { Message, ConfirmationService } from "primeng/primeng";
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';

/**
 * 大客户费用利息/捐赠历史查询-洪满义
 */
@Component({
    selector: 'bigCustomDonateHistoryQuery',
    templateUrl: './bigCustomDonateHistoryQuery.component.html',
    styleUrls: ['./bigCustomDonateHistoryQuery.component.scss'],
    providers: [BigCusHttpService, Commfunc, ConfirmationService, CusViewHttpService]
})
export class BigCustomDonateHistoryQueryComponent implements OnInit {
    custNoTen = custNoTen;//校验客户号
    checkNumber = checkNumber;//校验数字
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    detailPage: boolean = false;
    historyQueryBean: HistoryQueryBean = new HistoryQueryBean();
    totalNum: number;
    dataList: any = [];
    msgs: Message[] = []; //提示信息
    first: number = 0;//分页控制
    code: any;
    operaTeype: any = [];
    statusType: any = [];
    approvedType: any = [];
    userId: string;
    manageId: string;
    updateValue: any = [];//修改
    detailHeader: string;
    inValue: any;
    bigDonate: any = [];
    addDisplay:boolean=false;
    constructor(
        public bigCusHttpService: BigCusHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusViewHttpService,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.operaTeype = this.code['OPERATYPE'];//操作类型
        this.statusType = this.code['BIGSTATUSTYPE'];//状态
        this.approvedType = this.code['APPROVEDTYPE'];//审批状态
        this.bigDonate = this.code['BIGDONATE'];//大客户捐赠
    }

    ngOnInit() {
        this.historyQueryBean.pageNum = 1;
        this.historyQueryBean.pageSize = 10;
        this.first = 0;
        this.queryClick();
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    queryClick() {
        this.historyQueryBean.pageNum = 1;
        this.historyQueryBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        this.bigCusHttpService.bigCustInstDonateHisQuery(this.historyQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.totalNum = data.totalCount;
                this.dataList = data.bigCustResult;
                this.dataList.forEach((item,index) => {
                    this.bigCusHttpService.findUser({ custNo: this.dataList[index].custcode }).subscribe(dataUser => {
                        if (dataUser.returnCode.code == 'success') {
                            if (dataUser.infoList != null) {
                                if (dataUser.infoList.length > 0) {
                                    this.manageId = dataUser.infoList[0].manageId;
                                    this.dataList[index]['manageId'] = this.manageId;
                                    // console.log( this.dataTable);
                                }
                            }
                        } 
                    })
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //重置
    resetClick() {
        this.historyQueryBean = new HistoryQueryBean();
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.historyQueryBean.pageNum = 1;
        this.historyQueryBean.pageSize = 10;
        this.first = 0;
    }

    paginate　(event) {
        //每页显示的条数
        this.historyQueryBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.historyQueryBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.historyQueryBean.pageSize;
        this.query();
    }

    //校验客户号只能输入数字
    onKeyupNumber(item, event) {
        if (checkNumber(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
            event.target.value = '';
            return
        } else {
            if (item && item != '') {
                let param = {
                    custNo: item
                }
                this.httpService.selectCustBriefInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.historyQueryBean.custname = data.custName;
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        }
    }

    //回显客户名称
    // showName(item) {

    // }

    //删除 
    toDelete(item) {
        this.confirmationService.confirm({
            message: '确定要删除吗?',
            accept: () => {
                let param = {
                    seqno: item.seqno,
                }
                this.bigCusHttpService.delBigCustInstDonate(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                })
            }
        });
    }

    //查看详情
    toDetail(data) {
        this.detailHeader = '大客户详情界面'
        this.detailPage = true;
        this.inValue = data;
    }
    //修改
    toUpdate(data) {
        this.detailHeader = '大客户修改界面'
        this.addDisplay = true;
        this.updateValue = data;
    }

    //修改的回调
    updateCall(param) {
        this.addDisplay = param;
        this.query();
    }
    //校验大客户编号
    onKeyupSeqno(event) {
        if (custNoTen(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的编号!' }];
            event.target.value = '';
            return
        }
    }

    //校验存款账号     
    onKeyupAccountno(event) {
        if (checkNumber(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的存款账号!' }];
            event.target.value = '';
            return
        }
    }
    //绑定权限id
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
